import React from "react"
import { Grid, Button , Typography,  } from "@mui/material"
import { Facebook , Twitter , Instagram , Google } from "@mui/icons-material"

const Buttons =()=>{
    return(
        <Grid xs={11} sx={{ margin:"auto" }}>
<Typography
            sx={{ fontSize: "14px", color: "#000", textAlign: "center" }}
          >
            Or
          </Typography>
          <Button
            sx={{
              fontSize: "13px",
              fontWaight: "600",
              p: 1,
              color: "#fff",
              backgroundColor: "#003ab5",
              my: 2,
              borderRadius: "100px",
              width: "100%",
              textTransform: "none",
            }}
          >
            <Facebook sx={{ mx: 2 }} /> Continue with facebook
          </Button>
          <Button
            sx={{
              fontSize: "13px",
              fontWaight: "600",
              p: 1,
              color: "#fff",
              backgroundColor: "#141526",
              my: 2,
              borderRadius: "100px",
              width: "100%",
              textTransform: "none",
            }}
          >
            <Google sx={{ mx: 2 }} />
            Continue with Google
          </Button>
          <Button
            sx={{
              fontSize: "13px",
              fontWaight: "600",
              p: 1,
              color: "#fff",
              backgroundColor: "#47acdf",
              my: 2,
              borderRadius: "100px",
              width: "100%",
              textTransform: "none",
            }}
          >
            <Twitter sx={{ mx: 2 }} />
            Continue with Twitter
          </Button>
          <Button
            sx={{
              fontSize: "13px",
              fontWaight: "600",
              p: 1,
              color: "#fff",
              background: "linear-gradient( to right, #de04f4, #f7029c)",
              my: 2,
              borderRadius: "100px",
              width: "100%",
              textTransform: "none",
            }}
          >
            <Instagram sx={{ mx: 2 }} />
            Continue with Instagram
          </Button>
        </Grid>
    )
}
export default Buttons