import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { FormControlLabel, IconButton } from '@mui/material';
// import downArrow from './../../../icons/downArrow.svg'
import Collapse from '@mui/material/Collapse';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import { styled } from '@mui/material/styles';
import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import './PostInternshipDetails.css';

const ExpandMore = styled((props) => {
    const { expand, ...other } = props;
    return <IconButton {...other} />;
})(({ theme, expand }) => ({
    transform: !expand ? 'rotate(0deg)' : 'rotate(180deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
        duration: theme.transitions.duration.shortest,
    }),
}));

const PostInternshipDetails = () => {
    const [expanded, setExpanded] = useState(true);

    const handleExpandClick = () => {
        setExpanded(!expanded);
    };
    return (
        <div>
            <p className="faq_question">Post an internship?</p>
            <div className="d-flex justify-content-between align-items-center ">
                <p className="help_text">Need assistance? Please </p>
                <p>
                    <Link className="help_text text-primary" to="#" >visit Help Center</Link>
                </p>

            </div>
            <div className="d-flex justify-content-center align-items-center">
                <div className="internship_details_card">
                    <div className="d-flex justify-content-center align-items-center">
                        <div className="d-flex justify-content-between align-items-center internship_details_header">
                            <p className="mt-3">Internship details</p>
                            {/* <img src={downArrow} alt="" /> */}
                            <ExpandMore
                                expand={expanded}
                                onClick={handleExpandClick}
                                aria-expanded={expanded}
                                aria-label="show more"
                            >
                                <ExpandMoreIcon style={{ color: '#fff' }} />
                            </ExpandMore>
                        </div>
                    </div>

                    <Collapse in={expanded} timeout="auto" unmountOnExit>
                        <div className="collapse_check_item">
                        <FormControl component="fieldset">
                            <FormLabel component="legend">Profile</FormLabel>
                            <RadioGroup
                                aria-label="gender"
                                defaultValue="female"
                                name="radio-buttons-group"
                            >
                                <FormControlLabel value="Business Development (Sales)" control={<Radio />} label="Business Development (Sales)" />
                                <FormControlLabel value="Web Development" control={<Radio />} label="Web Development" />
                                <FormControlLabel value="Graphic Design" control={<Radio />} label="Graphic Design" />
                                <FormControlLabel value="Content Writing" control={<Radio />} label="Content Writing" />
                                <FormControlLabel value="Social Media Marketing" control={<Radio />} label="Social Media Marketing" />
                                <FormControlLabel value="Marketing" control={<Radio />} label="Marketing" />
                                <FormControlLabel value="Digital Marketing" control={<Radio />} label="Digital Marketing" />
                                <FormControlLabel value="Human Resources (HR)" control={<Radio />} label="Human Resources (HR)" />
                                <FormControlLabel value="Law/Legal" control={<Radio />} label="Law/Legal" />
                                <FormControlLabel value="Campus Ambassador" control={<Radio />} label="Campus Ambassador" />
                                <FormControlLabel value="Other" control={<Radio />} label="Other" />
                            </RadioGroup>
                        </FormControl>
                        </div>
                     
                    </Collapse>
                </div>
            </div>


            <div className="text-center mb-5 mt-3">
                <div>
                    <button className="save_draft_btn">Save draft</button>
                    <button className="post_internship_btn">Post Internship</button>
                </div>
            </div>
            <br /><br />
        </div>
    );
};

export default PostInternshipDetails;